* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}
:root {
  --primary: #8c54ff;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --text:#3b4348;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--light);
  /* background-image: linear-gradient(to bottom, #cfdeee 0%, #8da9cf 100%); */
  color: var(--text);
  /* font-family: 'Georgia Pro',Georgia, 'Times New Roman', Times, serif; */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
  display: inline-block;
  line-height: 1.5;
}

h2 {
  font-size: 2.5rem;
  font-weight: bold;
}

h3 {
  font-size: 1.75rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
	margin: 0 auto;
  padding: 20px 60px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin:0;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

/* header start */
header {
  background: var(--dark);
  padding: 10px 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  position:-webkit-sticky;
  position: sticky;
  top:0;
  width:100%;
  /* height: auto; */
  z-index: 9999; 
}

header h1 {
  text-decoration: none;
}

header a {
  color: var(--light);
  text-decoration: none;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items:center;
  list-style: none;
}
.navActive {
  color: var(--secondary);
}

/* header ends */

/* About me start */
.about-me {
  display: flex;
  flex-wrap: wrap;
  flex: 1;
}

.about-me h2 {
  flex: 1 100%;
}

.about-img {
  flex: 0 32%;
  margin-right:0;
  margin-top: 15px;
  margin-bottom: 15px;
}

.about-info p {
  line-height: 1.7;
  font-size: 1em;
  margin-bottom: 1.5rem;

}
/* About me ends */

/* contact-icons start */
.contact-icons {
  display: flex;
  justify-content: center;
  margin: 20px auto;
}

.contact-icons a {
  margin: 5px;
  display: flex;
  min-width: 80px;
  flex-direction: column;
  color: var(--light);
  text-decoration: none;
}

.contact-icons a span {
  text-align: center;
}
/* Contact-icons end */

/* portfolio start */
.project-box {
  box-sizing: border-box;
  position: relative;
  width: 33%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border:1px solid var(--text);
  border-radius: 10px;
}

.project-info {
  padding: 10px 0;
  height: 100%;
  background-repeat: no-repeat;
  background-position: bottom;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease;
  /* opacity: 0; */
}

.project-tag {
  position:absolute;
  display: none;
  background-color: rgba(0, 0, 0, .8);
  color: var(--light);
  font-weight: bold;
  height: 100%;
  width: 100%;
  text-align: center;
}

.project-box:hover .project-tag {
  display: block;
  margin: 0;
}

.project-link {
  background: none;
  font-size: 40px;
  margin-bottom: 5px;
  margin-right: 10px;
}
/* portfolio end */

/* Contact me start */
label{
  display: block;
  margin-top: 1.2rem;
}
input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input[type="email"]:valid {
  border-color: green;
}

input[type="email"]:invalid {
  border-color: red;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);

}
textarea {
	max-width: 100%;
	border: 1px solid var(--dark);
	padding: 5px;
  display: block;
}
button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 10px 1px;
  background-color: var(--secondary);
  color:var(--light);
}

button:hover {
  opacity: .8;
}
/* Contact me end */

/* Resume Start */
ul.resume-info {
  list-style: inside;
}

.resume-info p {
  display: inline;
}

ul.edu-info {
  list-style: none inside;
}
/* Resume Ends */

@media screen and (max-width:980px) {
  header {
    justify-content: center;
    padding-bottom: 0;
    padding:0;
  }

  header h1 {
    width:100%;
    text-align: center;
  }

  header nav ul {
    width: 100%;
    justify-content: center;
  }

  header nav ul li a {
    font-size: 20px;
  }
}

@media screen and (max-width:768px){
  header nav ul li a {
    font-size: 16px;
  }

  main {
    padding:0px 20px;
  }
}
  
@media screen and (max-width:575px) {
  
  header {
    padding:0;
  }
  
  /* body {
    line-height: 1.3; 
  } */
  
  .flex-row .project-box {
    width: 100%;
  }
  
  nav {
    width:100%;
  }
  nav li a {
    padding: 0px;
    text-align: center;
    
  }

  #contact-form input[type="text"], 
  #contact-form input[type="email"],
  #contact-form textarea,
  #contact-form button {
    width: 100%;
  }

  nav ul li{
    width: 25%; 
  }
} 
